import React from 'react';
import {Navbar, NavbarBrand, Nav} from 'react-bootstrap';
import {scroller} from 'react-scroll';

const Header = () => {

  const ScrollTo = (container) => {
    scroller.scrollTo(container,{
      duration: 800,
      delay: 0,
      smooth: 'easeInOutQuart',
      offset: -100
    });
  }
  return(
    <header id="home">
      <Navbar bg="dark" fixed="top" variant="dark" expand="lg">
        <NavbarBrand href="#">Caiçara Software Club</NavbarBrand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="mr-auto">
            <Nav.Link href="#" onClick={()=> ScrollTo('home')}>Home</Nav.Link>
            <Nav.Link href="#" onClick={()=> ScrollTo('about')}>Sobre</Nav.Link>
            <Nav.Link href="#" onClick={()=> ScrollTo('contact')}>Contato</Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
    </header>
  )
};

export default Header;
