import React from 'react';
import {scroller} from 'react-scroll';

const Footer = () => {

  const ScrollTo = (container) => {
    scroller.scrollTo(container,{
      duration: 800,
      delay: 0,
      smooth: 'easeInOutQuart',
    });
  }

  return(
    <footer class="container">
      <p class="float-right"><a href="#" onClick={()=> ScrollTo('home')}>Voltar</a></p>
      <p>©2020 Caiçara Software Club, Inc.</p>
    </footer>
  )
};

export default Footer;
