import React from 'react';
import devs from './devs';
import Contact from './../Contact';

const About = () =>{
  return(
    <div className="container marketing" id="about">

    <div className="row">
      <div className="col-lg-12"><h1 className="text-center mb-3">Devs</h1></div>
      {devs.map(dev => (
        <div key={dev} className="col-lg-4 mt-3">
          <img src={dev.image_url} alt={dev.name} className="bd-placeholder-img rounded-circle"/>
          <h2>{dev.name}</h2>
          <p>{dev.bio}</p>
          <a className="btn btn-outline-primary" href={dev.github_username} role="button">Perfil GitHub</a>
        </div>
      ))}

    </div>

    <hr className="featurette-divider"/>

    <Contact />

    <hr className="featurette-divider"/>

  </div>
  )
}

export default About;